@import url(https://fonts.googleapis.com/css2?family=VT323&display=swap);
@import url(https://fonts.googleapis.com/css2?family=VT323&display=swap);
.clock{position:fixed;top:0;right:0;left:0;height:100px;font-size:5em;font-family:'VT323', monospace;background-color:rgba(36,35,41,0.8);color:#fff;text-align:center;border-bottom:1px solid #413f47;-webkit-user-select:none;-ms-user-select:none;user-select:none;display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;-webkit-justify-content:center;justify-content:center;overflow:hidden}

.controls{position:fixed;bottom:0;right:0;background-color:rgba(36,35,41,0.8);color:#fff;text-align:center;border-top:1px solid #413f47;border-left:1px solid #413f47;-webkit-user-select:none;-ms-user-select:none;user-select:none}.controls button{font-size:1em;padding:6px 12px}.controls button:hover{background-color:#000097}

.timeline{padding:120px 0;overflow-x:hidden;overflow-y:scroll;list-style-type:none;margin:0;-webkit-user-select:none;-ms-user-select:none;user-select:none}.item{display:-webkit-flex;display:flex;border-bottom:1px solid #413f47;-webkit-align-items:center;align-items:center}.item .item-status{width:120px;-webkit-align-self:stretch;align-self:stretch;display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;-webkit-justify-content:center;justify-content:center;font-family:'VT323', monospace;font-size:1.3em}.item .message{padding:10px 20px 10px 0;-webkit-flex:1 1;flex:1 1}.item.late{background-color:#472c2a}.item.completed{background-color:#3f4741}.item:first-child{border-top:1px solid #413f47}.up-next{font-size:4vw}

body{background-color:#0d0c0f;color:white;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;margin:0;-moz-osx-font-smoothing:grayscale}html,body,#root,.App{height:100%;margin:0;padding:0;width:100%}.App{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column}.App header,.App footer{-webkit-flex:initial;flex:initial}.App section{margin-bottom:auto;overflow:auto}.key-hint{font-size:0.7em;font-weight:lighter}button{cursor:pointer;-webkit-user-select:none;-ms-user-select:none;user-select:none}input,button{background-color:transparent;border:none;color:white;outline:none}button:focus{background-color:#3f4741}.icon-status,.icon-depict{position:absolute;text-align:center;top:50%;-webkit-transform:translate(0, -50%);transform:translate(0, -50%);width:20px}.icon-status{left:10%}.icon-depict{right:10%}

